<template>
  <div>
    <h2 class="app-title">
      <img src="landmark.png">
      <span>武汉灵泉寺功德系统</span>
    </h2>
    <section>
      <h2 class="app-main-title">登录步骤</h2>
      <van-cell-group>
        <van-cell title="1、长按二维码，点击关注“释禅玉”公众号"/>
        <img :src="qrcode">
      </van-cell-group>
      <van-cell-group>
        <van-cell title="2、给公众号发送消息：“灵泉寺”，如图所示"/>
        <img :src="messagepic">
      </van-cell-group>
    </section>
  </div>
</template>

<script>
import { Cell, CellGroup } from "vant";
import Util from "../../js/utils";
import { router } from "../../router";
import axios from "axios";

export default {
  components: {
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup
  },

  data() {
    return {
      qrcode: window.PLATFORM_CONFIG.picUrl("qrcode"),
      messagepic: window.PLATFORM_CONFIG.picUrl("messagepic")
    };
  },

  methods: {},

  beforeMount() {
    let openid = localStorage["openid"];
    let user = Util.readLocal("user", window.PLATFORM_CONFIG.userExpire);

    if (openid == user.openid) {
      return router.push("work");
    }
    if (openid == null) return;
    axios({
      method: window.PLATFORM_CONFIG.api("user.getByOpenid.method"),
      url: window.PLATFORM_CONFIG.api("user.getByOpenid.url"),
      data: {
        openid
      }
    })
      .then(function(response) {
        if (response.data != "") {
          Util.storeLocal("user", response.data);
        }
      })
      .catch(function(error) {
        window.console.log(error);
      });
  }
};
</script>

<style lang="less">
</style>


